<template>
  <view class="chat-page">
    <!-- È°∂ÈÉ®ÂÆâÂÖ®Âå∫ÂüüÂç†‰ΩçÔºàÂ∞èÁ®ãÂ∫èÈªòËÆ§ÂØºËà™Ê†èÂ∑≤Áî± pages.json ËÆæÁΩÆÊ†áÈ¢òÔºâ -->

    <!-- ‰ºöËØùÂå∫ -->
    <scroll-view class="messages" scroll-y :scroll-top="scrollTop" scroll-with-animation @scrolltoupper="loadMore" :refresher-enabled="false">
      <view v-for="(msg, index) in messages" :key="index" :class="['msg', msg.type]">
        <image v-if="msg.type === 'assistant'" class="avatar" :src="assistantAvatar" mode="aspectFit" />
        <view v-else class="avatar-placeholder"></view>
        <view class="bubble">
          {{ msg.content }}
          <view v-if="msg.loading" class="typing-dots">
            <view class="dot"></view>
            <view class="dot"></view>
            <view class="dot"></view>
          </view>
        </view>
      </view>
    </scroll-view>

    <!-- ËæìÂÖ•Âå∫ -->
    <view class="input-bar">
      <input 
        class="input-field" 
        v-model="userInput" 
        placeholder="ËæìÂÖ•È£üÊùê/ÂÅèÂ•Ω"
        :disabled="isLoading"
        @confirm="sendMessage"
        confirm-type="send"
      />
      <button class="send-btn" :disabled="!userInput.trim() || isLoading" @click="sendMessage">
        {{ isLoading ? 'ÂèëÈÄÅ‰∏≠...' : 'ÂèëÈÄÅ' }}
      </button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      messages: [
        {
          type: 'assistant',
          content: '‰ªäÂ§©ÂêÉÁÇπ‰ªÄ‰πàÂë¢ÔºüÂèØ‰ª•ËæìÂÖ•È£üÊùêÊàñÂè£Âë≥ÂÅèÂ•ΩÔºåÊàëÊù•Áªô‰Ω†ÁÅµÊÑüÔΩû',
          loading: false
        }
      ],
      userInput: '',
      isLoading: false,
      scrollTop: 0,
      assistantAvatar: '/static/retouch_2025102716043091.png',
      userAvatar: '/static/yuan_97e57f821c79b841651df5b413309328.jpg'
    };
  },
  
  onLoad() {
    // ÂàùÂßãÂåñ‰ºöËØùID
    this.conversationId = uni.getStorageSync('conversationId') || undefined;
  },
  
  methods: {
    // Ë∞ÉÁî®AIÊé•Âè£
    async askAi(query, variables = {}) {
      try {
        const response = await uni.request({
          url: 'http://172.20.10.3:9000/api/ai/workflow/execute',
          method: 'POST',
          header: { 
            'Content-Type': 'application/json', 
            'Authorization': `Bearer ${uni.getStorageSync('token')}` 
          },
          data: {
            query,
            lang: 'zh',
            context: { 
              page: 'home', 
              conversationId: this.conversationId 
            },
            variables
          }
        });
        
        if (response[1]?.data?.success) {
          const resp = response[1].data.data;
          const convId = resp?.conversation_id || resp?.data?.conversation_id;
          
          // ‰øùÂ≠ò‰ºöËØùID
          if (convId) {
            this.conversationId = convId;
            uni.setStorageSync('conversationId', convId);
          }
          
          return resp;
        } else {
          throw new Error(response[1]?.data?.message || 'AIË∞ÉÁî®Â§±Ë¥•');
        }
      } catch (error) {
        console.error('AIË∞ÉÁî®Â§±Ë¥•:', error);
        throw error;
      }
    },
    
    // ÂèëÈÄÅÊ∂àÊÅØ
    async sendMessage() {
      if (!this.userInput.trim() || this.isLoading) return;
      
      const query = this.userInput.trim();
      this.userInput = '';
      
      // Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
      this.messages.push({
        type: 'user',
        content: query,
        loading: false
      });
      
      // Ê∑ªÂä†AIÂõûÂ§çÂç†‰Ωç
      const aiMessageIndex = this.messages.push({
        type: 'assistant',
        content: '',
        loading: true
      }) - 1;
      
      this.isLoading = true;
      this.scrollToBottom();
      
      try {
        const response = await this.askAi(query);
        
        // Ê†ºÂºèÂåñAIÂõûÂ§ç - Â¶ÇÊûúÊòØJSONÊ†ºÂºèÁöÑËèúË∞±Êï∞ÊçÆÔºåÂàôËøõË°åÁæéÂåñÂ±ïÁ§∫
        let content = response?.answer || response?.data?.answer || 'Êä±Ê≠âÔºåÊàëÊöÇÊó∂Êó†Ê≥ïÂõûÁ≠îËøô‰∏™ÈóÆÈ¢ò„ÄÇ';
        
        // Â∞ùËØïËß£ÊûêJSONÊ†ºÂºèÁöÑËèúË∞±Êï∞ÊçÆ
        try {
          if (typeof content === 'string') {
            const parsedData = JSON.parse(content);
            if (parsedData && typeof parsedData === 'object' && parsedData.name) {
              content = this.formatRecipeData(parsedData);
            }
          } else if (content && typeof content === 'object' && content.name) {
            content = this.formatRecipeData(content);
          }
        } catch (error) {
          // Â¶ÇÊûú‰∏çÊòØJSONÊ†ºÂºèÔºå‰øùÊåÅÂéüÊ†∑
        }
        
        this.messages[aiMessageIndex] = {
          type: 'assistant',
          content: content,
          loading: false
        };
        
      } catch (error) {
        // ÊòæÁ§∫ÈîôËØØ‰ø°ÊÅØ
        this.messages[aiMessageIndex] = {
          type: 'assistant',
          content: 'ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúÂêéÈáçËØï„ÄÇ',
          loading: false
        };
        
        uni.showToast({
          title: error.message || 'AIË∞ÉÁî®Â§±Ë¥•',
          icon: 'none'
        });
      } finally {
        this.isLoading = false;
        this.scrollToBottom();
      }
    },
    
    // ÊªöÂä®Âà∞Â∫ïÈÉ®
    scrollToBottom() {
      this.$nextTick(() => {
        setTimeout(() => {
          this.scrollTop = 99999;
        }, 100);
      });
    },
    
    // Âä†ËΩΩÊõ¥Â§öÊ∂àÊÅØÔºàÈ¢ÑÁïôÔºâ
    loadMore() {
      // ÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞Âä†ËΩΩÂéÜÂè≤Ê∂àÊÅØÁöÑÂäüËÉΩ
    },
    
    // Ê†ºÂºèÂåñËèúË∞±Êï∞ÊçÆ
    formatRecipeData(recipe) {
      let formatted = '';
      
      // 1. ËèúÂêçÊ†áÈ¢òÔºàÂçïÁã¨‰∏ÄË°åÔºâ
      if (recipe.name) {
        formatted += `üçΩÔ∏è **${recipe.name}**

`;
      }
      
      // 2. Âü∫Êú¨‰ø°ÊÅØÈÉ®ÂàÜ
      let hasBasicInfo = false;
      let basicInfoContent = '';
      
      if (recipe.typeId) {
        const typeMap = {
          1: 'ÂÆ∂Â∏∏Ëèú',
          2: 'Ê±§ÂìÅ',
          3: 'ÁîúÁÇπ',
          4: '‰∏ªÈ£ü',
          5: 'Â∞èÂêÉ'
        };
        basicInfoContent += `‚Ä¢ ÂàÜÁ±ª: ${typeMap[recipe.typeId] || 'ÂÖ∂‰ªñ'}
`;
        hasBasicInfo = true;
      }
      
      if (recipe.ingredients) {
        basicInfoContent += `‚Ä¢ È£üÊùêÊï∞Èáè: ${recipe.ingredients}Áßç
`;
        hasBasicInfo = true;
      }
      
      if (recipe.condiments) {
        basicInfoContent += `‚Ä¢ Ë∞ÉÂë≥Êñô: ${recipe.condiments}
`;
        hasBasicInfo = true;
      }
      
      if (hasBasicInfo) {
        formatted += `üìã **Âü∫Êú¨‰ø°ÊÅØ**
${basicInfoContent}
`;
      }
      
      // 3. Âà∂‰ΩúÊñπÊ≥ïÈÉ®ÂàÜ
      if (recipe.method) {
        formatted += `üë®‚Äçüç≥ **Âà∂‰ΩúÊñπÊ≥ï**
`;
        // Â§ÑÁêÜÂ∏¶ÊúâÂ∫èÂè∑ÁöÑÂà∂‰ΩúÊ≠•È™§
        const methodLines = recipe.method.split(/\d+\./).filter(line => line.trim());
        let methodContent = '';
        methodLines.forEach((line, index) => {
          methodContent += `${index + 1}. ${line.trim()}
`;
        });
        formatted += `${methodContent}
`;
      }
      
      // 4. Ëê•ÂÖªÊàêÂàÜÈÉ®ÂàÜ
      if (recipe.nutrition) {
        let hasNutrition = false;
        let nutritionContent = '';
        
        if (recipe.nutrition.calories) {
          nutritionContent += `‚Ä¢ ÁÉ≠Èáè: ${recipe.nutrition.calories}
`;
          hasNutrition = true;
        }
        if (recipe.nutrition.protein) {
          nutritionContent += `‚Ä¢ ËõãÁôΩË¥®: ${recipe.nutrition.protein}
`;
          hasNutrition = true;
        }
        if (recipe.nutrition.fat) {
          nutritionContent += `‚Ä¢ ËÑÇËÇ™: ${recipe.nutrition.fat}
`;
          hasNutrition = true;
        }
        if (recipe.nutrition.carbohydrates) {
          nutritionContent += `‚Ä¢ Á¢≥Ê∞¥ÂåñÂêàÁâ©: ${recipe.nutrition.carbohydrates}
`;
          hasNutrition = true;
        }
        
        if (hasNutrition) {
          formatted += `üìä **Ëê•ÂÖªÊàêÂàÜ**
${nutritionContent}
`;
        }
      }
      
      // 5. È£üÁî®Âª∫ËÆÆÈÉ®ÂàÜ
      if (recipe.suggestion) {
        formatted += `üí° **È£üÁî®Âª∫ËÆÆ**
${recipe.suggestion}

`;
      }
      
      // 6. Â∞èË¥¥Â£´ÈÉ®ÂàÜ
      if (recipe.tips) {
        formatted += `üåü **Â∞èË¥¥Â£´**
${recipe.tips}
`;
      }
      
      return formatted;
    }
  }
};
</script>

<style>
.chat-page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: #f7f2e7;
}

/* ‰ºöËØùÂå∫ */
.messages {
  flex: 1;
  padding: 24rpx 24rpx 12rpx 24rpx;
  box-sizing: border-box;
}
.msg {
  display: flex;
  margin-bottom: 20rpx;
}
.msg.assistant .avatar {
  width: 144rpx;
  height: 144rpx;
  margin-right: 16rpx;
  border-radius: 24rpx;
}
.bubble {
  max-width: 560rpx;
  padding: 18rpx 22rpx;
  border-radius: 18rpx 18rpx 18rpx 6rpx;
  background: #ffffff;
  color: #1f2937;
  font-size: 28rpx;
  line-height: 1.6;
  box-shadow: 0 6rpx 16rpx rgba(0,0,0,0.05);
  word-wrap: break-word;
  word-break: break-word;
  display: inline-block;
  width: fit-content;
}

/* ËæìÂÖ•Âå∫ */
.input-bar {
  padding: 16rpx 20rpx 28rpx 20rpx;
  display: flex;
  align-items: center;
  gap: 16rpx;
  background: #f7f7f9;
  box-sizing: border-box;
}
.input-field {
  flex: 1;
  height: 88rpx;
  border-radius: 44rpx;
  background: #fffcf0;
  color: #1f2937;
  border: 1rpx solid #ffd166;
  display: flex;
  align-items: center;
  padding: 0 24rpx;
  font-size: 28rpx;
}
.input-field:focus {
  border-color: #ffb347;
  outline: none;
  background: #fff8e1;
  box-shadow: 0 0 0 3rpx rgba(255, 179, 71, 0.2);
}
.input-field::placeholder {
  color: #b38f00;
}
.send-btn {
  height: 88rpx;
  padding: 0 28rpx;
  border-radius: 44rpx;
  background: linear-gradient(135deg, #ffcb6b 0%, #ffd966 100%);
  color: #fff;
  font-size: 28rpx;
  border: none;
  transition: all 0.2s ease;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}
.send-btn:disabled {
  opacity: 0.6;
  background: #e0d7c8;
}
.send-btn:not(:disabled):active {
  background: linear-gradient(135deg, #ffa726 0%, #ffb300 100%);
  transform: scale(0.98);
}

/* Áî®Êà∑Ê∂àÊÅØÊ†∑Âºè */
.msg.user {
  justify-content: flex-end;
}
.msg.user .avatar {
  width: 144rpx;
  height: 144rpx;
  margin-left: 16rpx;
  margin-right: 0;
  border-radius: 24rpx;
  border: 2rpx solid #ffd166;
}
.avatar-placeholder {
  width: 144rpx;
  height: 144rpx;
  margin-right: 16rpx;
  margin-left: 0;
}
.avatar-placeholder {
  width: 144rpx;
  height: 144rpx;
  margin-right: 16rpx;
  margin-left: 0;
}
.msg.user .bubble {
  border-radius: 18rpx 18rpx 6rpx 18rpx;
  background: linear-gradient(135deg, #ffb347 0%, #ffcc33 100%);
  color: #ffffff;
  box-shadow: 0 6rpx 16rpx rgba(255, 179, 71, 0.3);
  border: 1rpx solid #ffb347;
  max-width: none;
  width: auto;
  min-width: 80rpx;
  padding: 18rpx 22rpx;
}

/* ÊâìÂ≠óÂä®Áîª */
.typing-dots {
  display: inline-flex;
  gap: 6rpx;
  margin-left: 12rpx;
}
.dot {
  width: 8rpx;
  height: 8rpx;
  border-radius: 50%;
  background: #9aa3af;
  animation: typing 1.4s infinite ease-in-out;
}
.dot:nth-child(1) {
  animation-delay: -0.32s;
}
.dot:nth-child(2) {
  animation-delay: -0.16s;
}
@keyframes typing {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}
</style>